import React, {useMemo} from 'react';
import {ButtonLink, Drawer, PageTitle} from '../../units';
import {Card, Row, Col} from 'antd';
import {ReactTable} from '../../units';
import {formatDate} from '../../helpers';
import {useBundles} from '../../hooks';

export default function ViewBundles(props) {
    let name = props.match.params.name;

    const { status, data, error, isFetching } = useBundles(name);

    const actualData = data? data.data : [];

    const columns = useMemo(() => [
        {
            id: 'date',
            Header: 'Date Added',
            accessor: d => formatDate(d.updatedAt),
            Cell: d=><span>{d.value}</span>
        },
        {
            Header: 'Description',
            accessor: 'description',
        },
        {
            Header: 'Cost',
            accessor: 'billValue',
        },
        {
            Header: 'Validity',
            accessor: 'validityValue',
        },
        {
            id: 'validity',
            Header: 'Validity Unit',
            accessor: 'validity',
            Cell: d => <span>{d.value.name}</span>
        },
        {
            id: 'action',
            Header: 'Action',
            accessor: '_id',
            Cell: d => <span><ButtonLink type="primary" size="small" to="#" label="Edit" /></span>
        }
    ], actualData)

    return (
        <Drawer>
                 <Card>
                <Row gutter={24}>
                    <Col span={20}>
            <PageTitle title={name + " Data Bundle"} />
            </Col>
               <Col span={4} style={{padding: '10px'}}>
                <ButtonLink style={{borderRadius: '5px'}} type="primary" to="/users/create" label="New Bundle" />
                </Col>
            </Row>

            <ReactTable 
            columns={columns}
            data = {actualData}
            filterable={true}
            />
            </Card>
        </Drawer>
    )
}
